import React, { useState } from "react";
import { Plus } from "lucide-react";
import { navigate } from "@reach/router"; // ✅ ADD THIS
import Avatar from "../shared/Avatar/Avatar";
import { testUsers } from "./test-users";

const members = testUsers;

const DashboardTable: React.FC<{ query: string }> = ({ query }) => {
  const [tooltip, setTooltip] = useState<{ text: string; x: number; y: number } | null>(null);

  const filtered = members.filter(
    (m) =>
      (m.full_name ?? "").toLowerCase().includes(query.toLowerCase()) ||
      (m.hometown ?? "").toLowerCase().includes(query.toLowerCase()) ||
      (m.membership_level ?? "").toLowerCase().includes(query.toLowerCase())
  );

  const showTooltip = (e: React.MouseEvent, text: string) => {
    const rect = (e.currentTarget as HTMLElement).getBoundingClientRect();
    setTooltip({ text, x: rect.left + rect.width / 2, y: rect.top - 8 });
  };
  const hideTooltip = () => setTooltip(null);

  return (
    <section style={{ position: "relative" }}>
      <style>{`
        table {
          width: 100%;
          border-collapse: collapse;
          font-size: 13px;
          table-layout: fixed;
        }
        th, td {
          padding: 8px 14px;
          border-bottom: 1px solid var(--border);
          text-align: left;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          vertical-align: middle;
        }
        th {
          color: var(--muted);
          font-size: 11px;
          text-transform: uppercase;
        }
        tr {
          cursor: pointer; /* ✅ make it feel interactive */
          transition: background 0.2s ease;
        }
        tr:hover {
          background: var(--accent-bg);
        }

        .avatar-wrapper {
          width: 32px; height: 32px;
          border-radius: 50%;
          overflow: hidden;
          background: var(--border);
          display: flex; align-items: center; justify-content: center;
        }

        .pill {
          display: inline-flex;
          align-items: center;
          border-radius: 5px;
          padding: 2px 8px;
          font-size: 11px;
          white-space: nowrap;
        }
        .pill.Approved {
          background: rgba(16,185,129,0.15);
          color: #10b981;
        }
        .pill.Pending {
          background: rgba(245,158,11,0.15);
          color: #f59e0b;
        }
        .pill.Under-Review {
          background: rgba(59,130,246,0.15);
          color: #3b82f6;
        }
        .pill.Email-Not-Verified {
          background: rgba(156,163,175,0.15); /* light gray */
          color: #9ca3af;
        }

        .cell-text {
          display: block;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          cursor: default;
        }

        .tooltip {
          position: fixed;
          background: var(--panel);
          color: var(--text);
          border: 1px solid var(--border);
          padding: 5px 8px;
          font-size: 12px;
          border-radius: 5px;
          white-space: normal;
          max-width: 240px;
          z-index: 9999;
          pointer-events: none;
          box-shadow: 0 4px 10px rgba(0,0,0,0.15);
          transform: translate(-50%, -100%);
        }
      `}</style>

      <div
        className="table-header"
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          padding: "12px 16px",
          borderBottom: "1px solid var(--border)",
        }}
      >
        <div>
          <strong>Members</strong>
          <div style={{ fontSize: "11px", color: "var(--muted)" }}>
            Showing {filtered.length} of {members.length} total
          </div>
        </div>
      </div>

      <table>
        <thead>
          <tr>
            <th>#</th>
            <th>Avatar</th>
            <th>Full Name</th>
            <th>Title</th>
            <th>Expertise</th>
            <th>Hometown</th>
            <th>Membership</th>
            <th>Status</th>
            <th>Joined</th>
          </tr>
        </thead>
        <tbody>
          {filtered.map((m, i) => (
            <tr
              key={m.id}
              onClick={() => navigate(`${m.id}`)} // ✅ navigation here
            >
              <td>{i + 1}</td>
              <td>
                <div className="avatar-wrapper">
                  <Avatar
                    url={m.avatar_url || ""}
                    width={32}
                    height={32}
                    alt={m.full_name || "User Avatar"}
                  />
                </div>
              </td>

              {[
                m.full_name || "—",
                m.title || "—",
                m.field_of_expertise || "—",
                m.hometown || "—",
                m.membership_level,
              ].map((text, idx) => (
                <td key={idx}>
                  <span
                    className="cell-text"
                    onMouseEnter={(e) => showTooltip(e, text)}
                    onMouseLeave={hideTooltip}
                  >
                    {text}
                  </span>
                </td>
              ))}

              <td>
                <span
                  className={`pill ${m.profile_status.replace(/\s+/g, "-")}`}
                >
                  {m.profile_status}
                </span>
              </td>
              <td>
                {new Date(m.created_at).toLocaleDateString(undefined, {
                  year: "2-digit",
                  month: "2-digit",
                  day: "2-digit",
                })}
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {tooltip && (
        <div className="tooltip" style={{ left: tooltip.x, top: tooltip.y }}>
          {tooltip.text}
        </div>
      )}
    </section>
  );
};

export default DashboardTable;
